.kromac-spinner.spinner-lighter {
  .loader {
    background: #0d2323;
    animation: rotate 1s infinite linear;
    border-radius: 50%;
    width: 100%;
    height: 100%;

    &::before {
      content: '';
      position: absolute;
      width: 50%;
      height: 100%;
      top: 0;
      left: 0;
      box-shadow: none !important;
      background: linear-gradient(to top, transparent, #00fff9);
      background-size: 100px 180px;
      background-repeat: no-repeat;
      border-top-left-radius: 100px;
      border-bottom-left-radius: 100px;
    }

    &::after {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      top: 0;
      left: 45%;
      background: #00fff9;
      border: solid 1px #04cdca;
      border-radius: 50%;
      z-index: 10;
      transform: scale(1.5);
      box-shadow: 0 0 10px #00fff9, 0 0 20px #00fff9, 0 0 30px #00fff9,
        0 0 40px #00fff9, 0 0 50px #00fff9, 0 0 60px #00fff9, 0 0 70px #00fff9,
        0 0 80px #00fff9, 0 0 90px #00fff9, 0 0 100px #00fff9;
    }

    span {
      position: absolute;
      top: 20px;
      left: 20px;
      right: 20px;
      bottom: 20px;
      border-radius: 50%;
      box-shadow: inset 0 0 15px 5px rgba(0, 0, 0, .5);
    }
  }
}